<script>
import moment from 'moment';
import XLSX from "xlsx";

export default {
    name: 'home',
    components: {
    },
    props: {
    },
    //추가한 항목
    filters : {
      yyyyMMdd : function(value){
        // 현재 Date 혹은 DateTime 데이터를 javaScript date 타입화
        var js_date = new Date(value);

        // 연도, 월, 일 추출
        var year = js_date.getFullYear();
        var month = js_date.getMonth() + 1;
        var day = js_date.getDate();
        var hours = ('0' + js_date.getHours()).slice(-2);
        var minutes = ('0' + js_date.getMinutes()).slice(-2);
        var seconds = ('0' + js_date.getSeconds()).slice(-2);

        // 월, 일의 경우 한자리 수 값이 있기 때문에 공백에 0 처리
        if(month < 10){
          month = '0' + month;
        }

        if(day < 10){
          day = '0' + day;
        }

        // 최종 포맷 (ex - '2022-10-01 12:30:00')
        return year + '-' + month + '-' + day + " " + hours + ':' + minutes + ":" + seconds;
      }
    },
    data() {
        return {
          today: moment(new Date()).format("YYYY-MM-DD"),
          startDt: moment(new Date()).format("YYYY-MM-DD"),
          endDt: moment(new Date()).format("YYYY-MM-DD"),

          selectedProcess: "",
          processList: null,
          searchDataList: null
           
        }
    },
     mounted() {
       console.log("load");
       //1. 프로세스 목록 조회
       this.$http.get("/api/data/processList").then((response) => {
        if(response.data!=null){
          console.log("get processList success!");
          console.log(response.data);
          this.processList = response.data;

        }else{
          console.log("get processList fail!");
        }
       })

       //2. 데이터 조회 함수 호출
       this.searchBtnFunc();

    },
    methods: {
      // 데이터 조회 함수
      searchBtnFunc(){
        console.log("startDt: " + this.startDt);
        console.log("endDt: " + this.endDt);
        console.log("selectedProcess: " + this.selectedProcess);

        this.$http.get("/api/data/searchList?startDt="+this.startDt+"&endDt="+this.endDt+"&selectedProcess="+this.selectedProcess).then((response) => {
        this.searchDataList = {}
        if(response.data!=""){
          
          
          console.log("get searchList success!");
          console.log(response.data);
          // 데이터 가공 로직
          if(this.selectedProcess==''){
            console.log("프로세스 선택 : All");
            // 1. 타이틀
            this.searchDataList.title = Object.keys(response.data[0]).filter(d => d != 'PROCESS_NM' && d != 'PROCESS_ID' && d != 'RUNDATE');
            // 2. 로우
            this.searchDataList.rowData = response.data;
          }else{
            console.log("프로세스 선택 : " + this.selectedProcess);
            // 1. 타이틀
            this.searchDataList.title = Object.keys(response.data[0]).filter(d => this.selectedProcess === d.substring(0, d.indexOf("_")));
            // 2. 로우
            this.searchDataList.rowData = response.data;
          }
          
          console.log("=== result ===");
          console.log(this.searchDataList);
         
        }else{
          console.log("get searchList fail!");
          this.searchDataList.rowData = null;
        }
       })

      },

      // 엑셀다운로드 
      getExcel () {
          const dataWS = XLSX.utils.table_to_sheet(document.getElementById('report-table')); // table id를 넣어주면된다
          const wb = XLSX.utils.book_new();
          XLSX.utils.book_append_sheet(wb, dataWS, "report");
          XLSX.writeFile(wb, moment(this.startDt).format('YYYY/MM/DD')+"-"+ moment(this.endDt).format('MM/DD')+"_report"+".xlsx");
      },
    },
    created() {

       
    },
    beforeMount() {
       
    },
   
    
}
</script>

